import * as React from 'react';

import { render, fireEvent, screen, waitFor } from '@testing-library/react';
import '@testing-library/jest-dom';

import { Popover } from '../index';

test('Popover: should render content.', async () => {

  render(<Popover visible content={<div>World</div>} ><div>Hello</div></Popover>);
  expect(screen.queryByText('Hello')).toBeInTheDocument();
  expect(screen.queryByText('World')).toBeInTheDocument();

});

test('Popover: should trigger visible change for hover trigger.', async () => {

  const fn = jest.fn();

  render(<Popover trigger="hover" onVisibleChange={fn} content={<div>World</div>} ><div>Hello</div></Popover>);
  expect(screen.queryByText('Hello')).toBeInTheDocument();
  expect(screen.queryByText('World')).toBeNull();

  const divHello = screen.queryByText('Hello')!;
  fireEvent.mouseEnter(divHello);
  expect(fn).toHaveBeenLastCalledWith(true);
  expect(screen.queryByText('World')).toBeInTheDocument();

  fireEvent.mouseLeave(divHello);
  expect(fn).toHaveBeenLastCalledWith(false);
  expect(screen.queryByText('World')).toBeNull();

});

test('Popover: should trigger visible change for mouse-enter trigger.', async () => {

  const fn = jest.fn();

  render(<Popover trigger="mouse-enter" onVisibleChange={fn} content={<div>World</div>} ><div>Hello</div></Popover>);
  expect(screen.queryByText('Hello')).toBeInTheDocument();
  expect(screen.queryByText('World')).toBeNull();

  const divHello = screen.queryByText('Hello')!;
  fireEvent.mouseEnter(divHello);
  await waitFor(() => {
    expect(fn).toHaveBeenLastCalledWith(true);
    expect(screen.queryByText('World')).toBeInTheDocument();
  });

  const divWorld = screen.queryByText('World')!;
  fireEvent.mouseLeave(divHello);
  await new Promise(r => setTimeout(r, 50));
  expect(screen.queryByText('World')).toBeInTheDocument();

  fireEvent.mouseEnter(divWorld);
  await new Promise(r => setTimeout(r, 1000));
  fireEvent.mouseLeave(divWorld);

  await waitFor(()=>{
    expect(fn).toHaveBeenLastCalledWith(false);
    expect(screen.queryByText('World')).toBeNull();
  });

});
